/* Common styles */
.app {
  background-color: hsl(231, 100%, 97%);
  display: flex;
  flex-direction: column;
  width:100%;
  height: 100%;
}

.card-title {
  color:hsl(213, 96%, 18%);
  font-family: 'ubuntu-medium';
}

.card-subtitle {
  margin-top:0; 
  color:hsl(231, 11%, 63%);
}

.button-primary {
  color: white;
  background-color: hsl(213, 96%, 18%) ;
  border:none;
  height: 45px;
  min-width: 120px;
  border-radius: 5px;
  font-family: 'ubuntu-medium';
}

.button-primary:hover {
  cursor: pointer;
}

.button-primary:disabled {
  color: white;
  background-color: hsl(213, 96%, 18%) ;
  border:none;
  height: 45px;
  min-width: 120px;
  border-radius: 5px;
  opacity: 0.5
}

.button-primary:active {
  color: white;
  background-color: hsl(213, 96%, 18%) ;
  border:none;
  height: 45px;
  min-width: 120px;
  border-radius: 5px;
  font-size: 16.5px;
}

.button-secondary{
  color: hsl(231, 11%, 63%);
  background-color: white;
  border:none;
  font-family: 'ubuntu-medium';
  font-size: 16.5px;
}

.button-secondary:hover{
  color: hsl(213, 96%, 18%) ;
  background-color: white;
  border:none;
  font-family: 'ubuntu-medium';
  cursor: pointer;
  font-size: 16.5px;
}

.checkbox-container {
  margin: 0;
  padding: 0;
  margin-top: -1rem;
  margin-bottom: 1rem;
}

.checkbox-label-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width:100%;
}

.checkbox-label-title {
  display: flex;
  flex-direction: column;
  padding-left: 1rem;
  justify-content: space-between;
}

.checkbox-label-title .label-info {
  color:hsl(231, 11%, 63%);
  font-size: 12px;
}

.checkbox-label {
  position: relative;
  cursor: pointer;
  line-height: 18px;
  border-radius: 10px;
  border: 1px solid hsl(229, 24%, 87%);
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-right: 1rem;
  padding-left: 1.75rem;
}

.checkbox-label:hover {
  border: 1px solid hsl(243, 100%, 62%);
  cursor: pointer;
}

.checkbox-label:before {
  content: '';
  position: absolute;
  left: 0.5rem;
  width: 18px;
  height: 18px;
  margin: 3px;
  margin-left: 5px;
  border: 1px hsl(229, 24%, 87%) solid;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}

.checkbox-input {
  margin: 0;
  width: 0;
  height: 0;
  visibility: hidden;
}

.checkbox-input:hover {
  cursor: pointer;
}

.checkbox-input:checked + .checkbox-label{
  border-color:hsl(243, 100%, 62%);
  background-color: hsl(243, 100%, 62%, 0.05);
}

.checkbox-input:checked ~ .checkbox-label:before {
  background-image: url("data:image/svg+xml;base64,");
  background-color: hsl(243, 100%, 62%);
  border-color:hsl(243, 100%, 62%);
  -webkit-mask-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAxIDEiPjx0aXRsZT51bnRpdGxlZDwvdGl0bGU+PHBhdGggZD0iTTAsMFYxSDFWMEgwWk0wLjQ1LDAuNzRsLTAuMDguMDhMMC4yOCwwLjc0LDAuMTQsMC42bDAuMDgtLjA4TDAuMzYsMC42NWwwLjQxLS40MUwwLjg2LDAuMzJaIi8+PC9zdmc+");
  mask-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAxIDEiPjx0aXRsZT51bnRpdGxlZDwvdGl0bGU+PHBhdGggZD0iTTAsMFYxSDFWMEgwWk0wLjQ1LDAuNzRsLTAuMDguMDhMMC4yOCwwLjc0LDAuMTQsMC42bDAuMDgtLjA4TDAuMzYsMC42NWwwLjQxLS40MUwwLjg2LDAuMzJaIi8+PC9zdmc+");
}

.checkbox-input:disabled ~ .checkbox-label {
  color: rgba(0, 0, 0, 0.38);
}

.checkbox-input:disabled ~ .checkbox-label:before {
  border-color: rgba(0, 0, 0, 0.26);
}

.checkbox-input:checked:disabled ~ .checkbox-label:before {
  background-color: rgba(0, 0, 0, 0.26);
  background-clip: padding-box;
}

.checkbox-input ~ .checkbox-label:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

.checkbox-input:checked ~ .checkbox-label:after {
  background-color: hsl(243, 100%, 62%);
}

.checkbox-input:not(:checked) ~ .checkbox-label:after {
  background-color: hsl(213, 96%, 18%)
}

.form-instructions {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.form-text-input-error {
  text-align: right;
  color: hsl(354, 84%, 57%);
  font-family: 'ubuntu-medium';
  font-size: 10px;
}

.form .text-input {
  height: 18px;
  margin: 0.5rem 0 2rem 0;
  padding: 0.5rem 0.75rem;
  border: 0.5px solid hsl(229, 24%, 87%);
  border-radius: 5px;
  color: hsl(213, 96%, 18%);
  font-family: 'ubuntu-medium';
}

.form .text-input:focus-visible {
  border: 0.5px solid  hsl(243, 100%, 62%);
  outline-style:solid;
  outline-color: hsl(243, 100%, 62%);
}

.form .text-input:hover {
  border: 1px solid  hsl(243, 100%, 62%)
}

.form .text-input::placeholder {
  color: hsl(231, 11%, 63%);
  font-family: 'ubuntu-regular';
  font-weight: 700;
}

.form .text-input-error {
  height: 18px;
  margin: 0.5rem 0 2rem 0;
  padding: 0.5rem 0.75rem;
  border: 0.5px solid hsl(354, 84%, 57%);
  border-radius: 5px;
  font-family: 'ubuntu-regular';
  font-weight: 700;
  color: hsl(213, 96%, 18%)
}

.form .text-input-error:focus-visible {
  border: 0.5px solid hsl(354, 84%, 57%) ;
  outline: none;
}

.form .text-input-label {
  color:hsl(213, 96%, 18%);
  text-align: left;
  font-weight: 500;
}

hr{
  width: 100%;
  margin-top: 1rem;
  color : hsl(231, 11%, 63%)

}

.main-content {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.radio-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}

.radio-container {
  width: 100%;
  text-align: left;
}

.radio-input {
  display:none
}

.radio-option-card { 
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom:1rem;
  border: 1px solid hsl(229, 24%, 87%);
  border-radius: 5px;
}

.radio-option-card:hover{
  border: 1px solid hsl(243, 100%, 62%);
  border-radius: 5px;
  cursor: pointer;
}

.radio-input:checked + .radio-option-card{
  border: 1px solid hsl(243, 100%, 62%);
  background-color: hsl(228, 100%, 84%, 0.25);
  border-radius: 5px;
}

.radio-option-card-header {
  padding: 0.5rem 1rem;
}

.radio-option-card-label {
  padding: 0.5rem 0;
}

.radio-label-info {
  color:hsl(231, 11%, 63%);
  font-size:11px;
  margin-top:5px;
}

.radio-label-promo {
  font-size:11px;
  margin-top:5px;
}

.switch-container {
  border-radius: 10px;
  background-color:hsl(231, 100%, 99%);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-right: 0.5rem;
  font-family: 'ubuntu-medium';
  margin-bottom: 1rem;
}

.switch {
  position: relative;
  display: inline-block;
  width: 54px;
  height: 26px;
  margin: 0.75rem 2rem;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: hsl(213, 96%, 18%);
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 6px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: hsl(213, 96%, 18%);
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.switch-label {
  color: hsl(231, 11%, 63%);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.summary-button {
  background-color: hsl(243, 100%, 62%);
  font-size: 15px;
}

.summary-button:hover {
  background-color: hsl(243, 100%, 62%);
}

.summary-button:focus-visible {
  background-color: hsl(243, 100%, 62%);
}

.summary-button:disabled{
  background-color: hsl(243, 100%, 62%);
}

.summary-details {
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
  border-radius: 10px;
  background-color: hsl(231, 100%, 99%);
}

.summary-line-amount{
  margin-top: 1rem;
  font-size: 14px;
  color: hsl(213, 96%, 18%);
  font-family: 'ubuntu-medium';
}

.summary-line-details {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.summary-line-info {
  font-size: 14px;
  color: hsl(231, 11%, 63%);
  margin-top: 1rem;
}

.summary-line-plan {
  font-family: 'ubuntu-bold';
  color: hsl(213, 96%, 18%);
}

.summary-line-plan-link {
  font-size: 14px;
  color: hsl(231, 11%, 63%);
  text-decoration: underline;
  margin: 10px 0;
}

.summary-line-plan-link:hover {
  color: hsl(243, 100%, 62%);
  text-decoration: underline;
  font-size: 14px;
  cursor: pointer;
}

.summary-total {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 2rem;
  color: hsl(231, 11%, 63%);
  font-size: 14px;
}

.summary-total-amount {
  color: hsl(243, 100%, 62%);
  font-family: 'ubuntu-bold';
  font-size: 20px;
}

.summary-thank-you-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  height: 100%;
}

